<template>
  <a-card hoverable style="width: auto">
    <template slot="actions" class="ant-card-actions">
      <span key="comment-basic-like">
        <a-tooltip title="Like">
          <a-icon type="like" :theme="action === 'liked' ? 'filled' : 'outlined'" @click="like" />
        </a-tooltip>
        <span style="padding-left: '8px';cursor: 'auto'">
          {{ likes }}
        </span>
      </span>
      <span key="comment-basic-dislike">
        <a-tooltip title="Dislike">
          <a-icon
              type="dislike"
              :theme="action === 'disliked' ? 'filled' : 'outlined'"
              @click="dislike"
          />
        </a-tooltip>
        <span style="padding-left: '8px';cursor: 'auto'">
          {{ dislikes }}
        </span>
      </span>
      <a-icon key="message" type="message" />
    </template>
    <a-card-meta title="2021年7月16 阴" description="随记">
      <a-avatar slot="avatar" style="color: #f56a00; backgroundColor: #fde3cf">
        mingo
      </a-avatar>
    </a-card-meta>
    <div class="words-content">
      &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;先帝创业未半而中道崩殂，今天下三分，益州疲弊，此诚危急存亡之秋也。然侍卫之臣不懈于内，忠志之士忘身于外者，盖追先帝之殊遇，欲报之于陛下也。诚宜开张圣听，以光先帝遗德，恢弘志士之气，不宜妄自菲薄，引喻失义，以塞忠谏之路也。
    </div>
    <audio src="https://music.163.com/song/media/outer/url?id=463821406" controls="controls">播放音乐</audio>
    <div class="image-list">
      <div class="image-item">
        <img
            alt="example"
            src="https://gw.alipayobjects.com/zos/rmsportal/JiqGstEfoWAOHiTxclqi.png"
        />
      </div>
      <div class="image-item">
        <img
            alt="example"
            src="https://gw.alipayobjects.com/zos/rmsportal/JiqGstEfoWAOHiTxclqi.png"
        />
      </div>
      <div class="image-item">
        <img
            alt="example"
            src="https://gw.alipayobjects.com/zos/rmsportal/JiqGstEfoWAOHiTxclqi.png"
        />
      </div>
      <div class="image-item">
        <img
            alt="example"
            src="https://gw.alipayobjects.com/zos/rmsportal/JiqGstEfoWAOHiTxclqi.png"
        />
      </div>
    </div>
  </a-card>
</template>

<script>
export default {
name: "ContentCard",
  data() {
    return {
      likes: 0,
      dislikes: 0,
      action: null,
    };
  },
  methods: {
    like() {
      this.likes = 1;
      this.dislikes = 0;
      this.action = 'liked';
    },
    dislike() {
      this.likes = 0;
      this.dislikes = 1;
      this.action = 'disliked';
    },
  },
}
</script>

<style scoped>
.image-item {
  width: 200px;
  height: 200px;
  /* 如果需要让图片各方位居中 */
  justify-content: center;
  align-items: center;
  display: inline;
}
.image-item img {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
  padding: 20px;
}
.words-content {
  padding: 20px;
}
</style>